.emailrow{
    display: flex;
    align-items: center;
    height: 50px;
    border-bottom: 1px solid whitesmoke;
    cursor: pointer;
    z-index: 999;
}
.emailrow:hover{
    border-top: 1px solid whitesmoke;
    box-shadow: 0px 4px 4px -2px rgba(0, 0, 0, 0.24);
}
.emailrow__options{
    display: flex;   
}
.emailrow__message{
    display: flex;
    flex: 0.1;
    align-items: center;
    font-size: 13px;
}
.emailrow__message>h4{
    width: 400px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    padding-left: 5px;
    padding-right: 5px;
}
.emailrow__title{
    font-size: 13px;
    flex: 0.3;
}
.emailrpw__time{
    padding-right: 15px;
    font-size: 9px;
    font-weight: bold;
}
.emailrpw__description{
    font-weight: 400;
    color: gray;

}